@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

::-webkit-scrollbar {
    width: 8px;
    height: 8px;
}

::-webkit-scrollbar-track {
    background: #0b0c10;
}

::-webkit-scrollbar-thumb {
    background: #66fcf1;
    background: rgba(255, 255, 255, 0.564);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.3);
}

.flex {
    display: flex;
    justify-content: center;
    align-items: center;
}

.App {
    flex-direction: column;
}

/* Navbar CSS */
.navbar {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    height: 10vh;
    width: 100vw;
    background-color: black;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    opacity: 90%;
}

.list {
    list-style: none;
    display: flex;
    width: 15vw;
    align-items: center;
    justify-content: space-between;
}

.logo {
    padding-left: 1vw;
}

.logo img {
    margin-top: 3px;
    border-radius: 15px;
    width: 80px;
    height: 10vh;
}

.list li {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    /* width: 55px; */
    height: 4vh;
    border-radius: 10px;
    cursor: pointer;
    font-size: 25px;
}

#home:link,
#about:link {
    text-decoration: none;
    color: rgb(159, 154, 154);
}

#home:visited,
#about:visited {
    text-decoration: none;
    color: rgb(135, 130, 130);
}

/* .list li:hover {
    font-weight: 1000;
    border: 2px solid white;
    color: white;
} */

#cartimg {
    width: 30px;
    height: 30px;
}

.mycart {
    background-color: black;
    color: white;
    border: transparent;
    margin-right: 2vw;
    cursor: pointer;
}

/* End Navbar CSS */

.images {
    height: 350px;
    width: 98vw;
    margin: 15px;
}

.images img {
    max-width: 100%;
    height: 350px;
    object-fit: fill;
    border-radius: 22px;
    box-shadow: inset 2px 2px 2px 0px rgba(255, 255, 255, .5),
        7px 7px 20px 0px rgba(0, 0, 0, .1),
        4px 4px 5px 0px rgba(0, 0, 0, .1);
}

.home {
    width: 98vw;
    position: relative;
    top: 10vh;
}

.home p {
    padding-left: 15px;
    font-size: 30px;
}

.container {
    display: flex;
    overflow-x: scroll;
    margin-bottom: 50px;
    background-color: #5605ffc2;
    border-radius: 12px;
    box-shadow: inset 2px 2px 2px 0px rgba(255, 255, 255, .5),
        7px 7px 20px 0px rgba(0, 0, 0, .1),
        4px 4px 5px 0px rgba(0, 0, 0, .1);
}

.card {
    height: 200px;
    min-width: 175px;
    margin: 15px;

    /* Glass Effect */
    background: rgba(255, 255, 255, 0.2);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.3);
    transition: all 0.1s ease-in-out;
}

.card:hover {
    transform: scale(1.07);
    cursor: pointer;
}

.card img {
    height: 100px;
    width: 150px;
    border-radius: 15px;
}

.card p {
    font-size: 18px;
    /* background-color: red; */
    text-align: center;
}

/* Card View */

.card1 {
    display: flex;
    justify-content: center;
    align-items: center;
    color: black;
    border: 1px solid white;
    width: 40%;
    height: 50vh;
    background-color: whitesmoke;
    border-radius: 10px;
    box-shadow: 0px 20px 20px 0px rgba(0, 0, 0, 0.2);
}

#product {
    width: 200px;
    height: 200px;
    border-radius: 7px;
}

.box1 {
    /* background-color: red; */
}

.box2 {
    width: 40%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
    height: 20vh;
}

.box2 span {
    display: fixed;
    top: 0;
}

.name {
    font-size: larger;
    font-weight: 600;
}

.addcart {
    width: 100px;
    height: 35px;
    background-color: orangered;
    color: white;
    border-radius: 5px;
}

#price {
    padding-bottom: 20px;
}

/* End Card View */

.cardView {
    width: 98vw;
    height: 98vh;
    /* background-color: rgba(244, 244, 246, 0.416); */
    position: fixed;
    top: 0;
    background: rgba(255, 255, 255, 0.226);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.3);
}

.cardView .view {
    width: 40vw;
    height: 50vh;
    /* flex-direction: column; */
    position: relative;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.3);
}

.view button {
    padding: 12px;
    border-radius: 12px;
    margin: 5px;
    cursor: pointer;

    background: #5405ff76;
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.3);
}

.view span {
    font-size: 35px;
    position: absolute;
    top: 0;
    right: 25px;
    cursor: pointer;
}

.emptyCart {
    font-size: 25px;
    padding: 25px;
    flex-direction: column;
    height: 50vh;
}

.emptyCart button {
    border: 0;
    border-radius: 7px;
    height: 30px;
    width: 100px;
    cursor: pointer;
    font-size: 17px;
    margin-top: 30px;
}

.orderCart .orderItem {
    display: block;
}

.orderCart .orderBox {
    align-items: flex-start;
}

.order {
    background-color: #beadfa;
    width: 100vw;
    min-height: 100vh;
    padding: 20px;
    padding-top: 12vh;
}

.orderBox {
    justify-content: space-around;
}

.orderItemCard {
    height: 100%;
    padding: 20px;
    margin: 10px;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.3);
}

.orderItem {
    display: flex;
    justify-content: space-around;
    /* background-color: red; */
}

.orderItem img {
    /* height: 200px; */
    width: 150px;
    border-radius: 22px;
    margin: 10px;
}

.orderItem .orderItemInfo {
    font-size: 28px;
    text-align: center;
}

.orderItemInfo span {
    font-size: 15px;
}

.orderCart .quantity {
    margin: auto;
}

.orderItem .quantity {
    width: 60%;
}

.quantity button {
    height: 30px;
    width: 40px;
    border-radius: 50%;
    border: 0;
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
}

.quantity p {
    padding: 0 5px;
    /* background-color: red; */
    width: 50px;
    text-align: center;
    font-size: 20px;
}

.orderForm {
    flex-direction: column;
}

.orderForm div {
    display: flex;
    align-items: center;
    padding: 15px;
    margin: 10px;
    width: 350px;
    display: flex;
    justify-content: space-between;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 16px;
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.3);
}

.orderForm div:last-child {
    /* background-color: red; */
    background: rgba(0, 0, 0, 0);
    background: #beadfa;
    backdrop-filter: blur(0);
    -webkit-backdrop-filter: blur(0);
    box-shadow: 0;
    border: 0;
}

.orderForm div input {
    padding: 8px;
    border-radius: 7px;
    border: 0;
    outline: none;
    background: rgba(13, 0, 255, 0.2);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.3);
}

.orderForm div button {
    width: 125px;
    border-radius: 12px;
    border: 0;
    padding: 3px 8px;
    cursor: pointer;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.3);
    transition: all 0.2s ease;
}

.orderForm div button:hover {
    transform: scale(1.03);
}

.confirmOrder {
    font-size: 30px;
    width: 600px;
    flex-direction: column;
    padding-left: 65px;
}

.confirmOrder button {
    border-radius: 7px;
    border: 0;
    width: 180px;
    height: 30px;
    font-size: 15px;
    margin: 15px;
    padding: 0 10px;
    cursor: pointer;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.3);
    transition: all 0.2s ease;
}

.confirmOrder button:hover {
    transform: scale(1.03);
}

.orderDetails {
    padding-top: 25px;
}

.orderDetails p:first-of-type {
    font-weight: bold;
}

.orderDetails p {
    font-size: 20px;
    padding-top: 10px;
}

.cart {
    background-color: #beadfa;
    border-radius: 12px;
    padding: 0 10px;
    margin-top: 12vh;
}

.cartItems {
    flex-direction: column;
    padding: 10px;
}

.cartItems ul li {
    height: 200px;
    margin: 20px;
    width: 500px;
    list-style: none;
    border-radius: 22px;
}

.cartItems img {
    height: 120px;
}

.cartItem {
    height: 100%;
    justify-content: space-around;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.3);
}

.cartItemInfo {
    /* background-color: red; */
}

.cartItemInfo p {
    font-size: 25px;
    text-align: center;
}

.moreCartItems p {
    font-size: 25px;
}

.moreCartItems .flex {
    justify-content: space-around;
}

.moreCartItems button {
    padding: 12px;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.3);
    cursor: pointer;
}

button:hover {
    transition: all 0.2s ease-in;
    transform: scale(1.02);
}